@mixin dl {
  @media only screen and (max-width: "1450px") {
    @content; } }
@mixin d {
  @media only screen and (max-width: "1240px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(AvertaRegular, AvertaRegular);
@mixin f {
  font-family: 'AvertaRegular', serif; }

@include font(AvertaLight, AvertaLight);
@mixin f3 {
