    .icon {
      font-size: 32px;
      fill: #1A1919; } }
  &__row {
    display: flex;
    align-items: flex-start;
    @include m {
      display: block; } }
  &__col {
    display: flex;
    flex: 0 0 50%;
    flex-wrap: wrap;
    @include m {
      display: block; } }
  &__insta,
  &__twitter {
    margin: 0 4px 8px; } }

.article {
  padding: 20px;
  &__content {
    h1,
    h2,
    h3 {
      font-weight: 400; }
    h1 {
      margin-bottom: 20px;
      @include f7;
      font-size: 30px;
      @include m {
        font-size: 22px; } }
    h2 {
      margin-bottom: 15px;
      @include f6;
      font-size: 20px;
      @include m {
        font-size: 16px; } }
    h3 {
      margin-bottom: 11px;
      @include f7;
      font-size: 17px;
      @include m {
        font-size: 15px; } }
    img {
      display: block;
      margin: 0 -20px 40px;
      width: calc(100% + 40px); }
    p,
    ul,
    ol {
      margin-bottom: 37px;
      font-size: 16px;
      color: #4A4A4A;
      line-height: (26/15);
      @include m {
        font-size: 13px; } }
    blockquote {
      margin-bottom: 40px;
      padding: 23px 0;
      border: solid rgba(#979797,.22);
      border-width: 1px 0;
      @include f7;
      font-size: 20px;
      line-height: (24/20);
      color: #4B626C; } }
  &__promo {
    padding: 15px 0 40px; } }

.tags {
  padding: 5px 15px 20px;
  &__head {
    margin-bottom: 9px;
    text-align: center; }
  &__icon {
    margin-bottom: 11px;
    font-size: 0;
    .icon {
      font-size: 32px; } }
  &__title {
    @include f6;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 4px;
    line-height: (24/14); }
  &__item {
    @include f6;
    color: #4A4A4A;
    &_green {
      color: $green; } } }

.post {
  display: flex;
  max-width: 946px;
  margin: 0 auto;
  @include m {
    display: block; }
  &__article {
    background: white;
    flex: 0 0 calc(100% - 256px); }
  &__aside {
    margin-left: 8px;
    background: white;
    flex: 0 0 248px;
    @include m {
      margin: 8px 0 0; } }
  &__head {
    padding: 12px;
    text-align: center; }
  &__icon {
    margin-bottom: 9px;
    font-size: 0;
    .icon {
      font-size: 22px;
      fill: #C29B6E;
      &-magic {
        font-size: 32px; } } }
  &__category {
    @include f6;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 4px;
    line-height: (24/14); }
  &__item {
    display: flex;
    margin-bottom: 10px;
    border: solid rgba(#979797,.16);
    border-width: 1px 0;
    font-size: 13px;
    color: #4A4A4A;
    align-items: center; }
  &__preview {
    flex: 0 0 40%; }
  &__pic {
    display: block;
    width: 100%; }
  &__wrap {
    padding: 10px;
    flex: 0 0 60%; }
  &__title {
    @include f6; }
  &__type {
    @include f3; }
  &__promo {
    padding: 15px 0; } }







